#sidebar {
	width: 100%;
	transition: 0.2s all ease-in-out;
	@include fixed(100%,0,45px,0);
	background: var(--background-color);
	z-index: 101;
	overflow-y: auto;
	opacity: 0;
    visibility: hidden;
    @media (max-width: 999px) {
    	padding: 40px 16px 16px;
    }
	&.active {
		top: 0;
		opacity: 1;
		visibility: visible;
		.sidebar-inner {
			margin-top: 0;
		}
		.sidebar-close {
			display: block;
		}
	}
	.sidebar-close {
		@include ab(0,n,n,0);
		width: 40px;
		height: 40px;
		display: none;
	}
	@media (min-width: 1000px) {
		position: unset;
		opacity: 1;
		visibility: visible;
		overflow: unset;
		max-width: 250px;
		z-index: unset;
		&.active {
			.sidebar-inner {
				margin-top: 30px;
			}
		}
	}
}
.sidebar-inner {
	margin-top: 30px;
	collection-filter {
		>div:not(:last-child) {
			margin-bottom: 30px;
			padding-bottom: 30px;
			border-bottom: 1px solid #F0F2F5;
		}
	}
}
.filter-block {
	h4 {
		font-size: 17px;
		line-height: 1.5em;
		font-weight: 500;
		margin-bottom: 15px;
	}
}

.block-menu {
	li {
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		>* {
			width: 100%;
		}
		&.has-dropdown {
			a {
				width: calc(100% - 30px);
			}
			summary {
				>.flex {
					align-items: center;
				}
			}
		}
		.submenu {
			li {
				>a {
					width: 100%;
					&:hover {
						color: var(--primary-color);
					}
				}
			}
		}
	}
	a {
		color: var(--text-color);
		font-weight: 500;
		font-size: 17px;
		line-height: 1.5em;
	}
	.menu-list {
		@include padding-start(15px);
		a { 
			font-size: 15px;
			line-height: 1.6em;
		}
		li {
			margin: 5px 0 6px;
		}
		.menu-list {
			a {
				font-weight: 400;
			}
		}
	}
	.block-inner {
		>ul {
			padding: 0;
			>li {
				details[open] {
					>summary >a {
						color: var(--primary-color);
						text-decoration: underline;
						text-underline-offset: 1px;
					}
					>summary {
						margin-bottom: 10px;
					}
					>.submenu {
						margin-bottom: 10px;
					}
					>summary >svg {
						path {
							fill: var(--primary-color);
						}
					}
				}
				>a {
					&:hover {
						color: var(--primary-color);
					}
				}
			}
		}
	}
	summary {
		align-items: center;
		&:hover {
			a {
				color: var(--primary-color);
			}
			svg path {
				fill: var(--primary-color);
			}
		}
	}
}

.range-price-slider {
	width: 100%;
	position: relative;
	margin-top: 21px;
	input {
		padding: 0;
		appearance: none;
		width: 100%;
		height: 4px;
		background: #F7F7F7;
		outline: none;
		opacity: 0.7;
		border-radius: 5px;
		transition: opacity .2s;
	}
	input::-webkit-slider-thumb {
		-webkit-appearance: none;
		appearance: none;
		width: 16px;
		height: 16px;
		background: #FE8F17;
		cursor: pointer;
		border-radius: 2px;
	}
	input::-moz-range-thumb {
		width: 16px;
		height: 16px;
		background: #FE8F17;
		cursor: pointer;
		border-radius: 2px;
	}
}
.range-price-textbox {
	display: flex;
	align-items: center;
	padding-top: 26px;
	>div{
		background: var(--miscellaneous-color);
		display: flex;
		border-radius: 5px;
		flex: 1;
		padding: 0 13px;
		color: var(--subtext-color);
	}
	.price-dash {
		padding: 0;
		background: none;
		flex: unset;
		font-weight: 500;
	}
	.field-currency {
		display: flex;
		align-items: center;
		font-weight: 400;
		font-size: 14px;
		line-height: initial;
	}
	input {
		padding: 0;
		font-size: 14px;
		font-weight: 400;
	}
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		appearance: none;
		margin: 0;
	}
}
.filter-list {
	label {
		margin: 0;
		@include padding-start(30px);
		width: 100%;
		cursor: pointer;
		transition: 0.2s all ease-in-out;
		&:before {
			content: "";
			@include ab(0,0,n,n);
			width: 20px;
			height: 20px;
			border: 1px solid #E3E3E3;
			border-radius: 4px;
			transition: 0.25s all ease-in-out;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
	input:checked + label {
		font-weight: 700;
		&:before {
			content: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.94175 1.35057L3.53528 7.75704L0 4.20939L1.35293 2.86118L3.53764 5.05353L8.59118 0L9.94175 1.35057Z' fill='white'/%3E%3C/svg%3E%0A");
			background: var(--primary-color);
			border-color: var(--primary-color);
		}
	}
	li {
		font-size: 14px;
		line-height: 1.5em;
		font-weight: 400;
		color: var(--text-color);
		position: relative;
		margin: 10px 0;
		cursor: pointer;
		&:hover {
			label {
				color: var(--primary-color);
				&:before {
					border-color: var(--primary-color);
				}
			}
		}
	}	
}
